import React, { Component } from 'react';
import withFirebaseAuth from 'react-with-firebase-auth'
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import firebaseConfig from '../firebaseConfig';
import StreamService from "../StreamService";

let firebaseApp = '';
if (!firebase.apps.length)
  firebaseApp = firebase.initializeApp(firebaseConfig);
else
  firebaseApp = firebase.app();
//const db = firebaseApp.firestore();

class Submit extends Component {
  constructor(props){
		super(props);
		this.state = {
  		leagues: null,
      error: false,
      game: null,
      stream_id:this.props.user ? this.props.user.uid+this.state.game : null,
      user_id:this.props.user ? this.props.user.uid : null,
      username:this.props.user ? this.props.user.displayName : null,
      mobile_compatible:"Yes",
      ads_overlay:0,
      nsfw:"Yes",
      misr:"HD",
      adblock:"ON",
      language:"Eng",
      channel_name:"DAZN",
      stream_link:"",
      stream_type:"Web Link",
      verified:"Non-Verified",
      success:false
  	};
	}

	handleChange = e => {
		this.setState({[e.target.name]: e.target.value});
	}
	handleSubmit = async e => {
		e.preventDefault();
    this.setState({error:false,success:false});
    const stream = {
      stream_id:this.props.user ? this.props.user.uid+this.state.game : null,
      user_id:this.props.user ? this.props.user.uid : null,
      username:this.props.user ? this.props.user.displayName : null,
      mobile_compatible:this.state.mobile_compatible,
      ads_overlay:this.state.ads_overlay,
      nsfw:this.state.nsfw,
      misr:this.state.misr,
      adblock:this.state.adblock,
      language:this.state.language,
      channel_name:this.state.channel_name,
      stream_link:this.state.stream_link,
      stream_type:this.state.stream_type,
      verified:"Non-Verified"
    }
    try{
      await StreamService.insertStream(this.state.game,stream);
      this.setState({success:true});
    }
    catch(err){
      console.log(err.status);
      this.setState({error:true});
    }
	}
	async componentDidMount(){
    try {
      this.setState({leagues: await StreamService.getNBAFixture()});
    } catch (err) {
      this.state.error = err.message;
    }
	}
 render() {
   const {
     user,
     signInWithGoogle,
   } = this.props;

   return (
    <div className="league">
    {this.state.error ? <div className="notification has-text-danger" style={{backgroundColor: '#1c1f22'}}>Error !! stream link is already submited</div> : ('')}
    {this.state.success ? <div className="notification has-text-success" style={{backgroundColor: '#1c1f22'}}>Stream link is successfuly submited</div> : ('')}

         {
           user
             ? ''
             : <button className="button button tag is-warning comp" style={{height: 'inherit',fontSize: '14px'}} onClick={signInWithGoogle}>Sign in with Google to Submit</button>
         }
         {
           user && this.state.leagues
             ? (<form className="send-chat" onSubmit={this.handleSubmit}>
              <div className="control" style={{width:'100%'}}>
                <label>Game</label>
                <select className="select selects" name="game" style={{width:'100%'}} onChange={this.handleChange} required>
                  <option>Select a Game</option>
                  {this.state.leagues[0].events.map((event) => (<option key={event.id} value={event.id}>{event.competitors[0].displayName} vs {event.competitors[1].displayName}</option>))}
                </select>
              </div>

              <div className="control" style={{width:'100%'}}>
                <label>Link</label>
                <input className="inputs input is-hovered" type="url" name="stream_link" onChange={this.handleChange} required/>
              </div>
              <div style={{display:'flex'}}>
                <div className="control" style={{width:'30%'}}>
                  <label>Mobile</label>
                  <select className="selects select" name="mobile_compatible" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="Yes">Yes</option>
                    <option value="No">No</option>
                  </select>
                </div>
                <div className="control" style={{width:'30%',marginLeft: '5%',marginRight: '5%'}}>
                  <label>Misr</label>
                  <select className="selects select" name="misr" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="HD">HD</option>
                    <option value="520p">520p</option>
                    <option value="SD">SD</option>
                  </select>
                </div>
                <div className="control" style={{width:'30%'}}>
                  <label>NSFW</label>
                  <select className="selects select" name="nsfw" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="Yes">Yes</option>
                    <option value="No">No</option>
                </select>
                </div>
              </div>
              <div style={{display:'flex'}}>
                <div className="control" style={{width:'45%',marginRight: '5%'}}>
                  <label>Ads</label>
                  <input className="inputs input is-hovered" type="number" name="ads_overlay" onChange={this.handleChange} value="0" required/>
                </div>
                <div className="control" style={{width:'45%',marginLeft: '5%'}}>
                  <label>Channal Name</label>
                  <input className="inputs input is-hovered" type="text" name="channel_name" onChange={this.handleChange} required/>
                </div>
              </div>
              <div style={{display:'flex'}}>
                <div className="control" style={{width:'30%'}}>
                  <label>Language</label>
                  <select className="selects select" name="language" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="Eng">English</option>
                    <option value="Fra">French</option>
                    <option value="Ita">Italien</option>
                    <option value="Spa">Spannish</option>
                    <option value="Ger">German</option>
                    <option value="Rus">Russian</option>
                    <option value="Ara">Arabic</option>
                    <option value="Ukr">Ukranian</option>
                    <option value="Por">Portuguese</option>
                    <option value="Oth">Other</option>
                  </select>
                </div>
                <div className="control" style={{width:'30%',marginLeft: '5%',marginRight: '5%'}}>
                  <label>Adblock</label>
                  <select className="selects select" name="adblock" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="ON">ON</option>
                    <option value="OFF">OFF</option>
                </select>
                </div>
                <div className="control" style={{width:'30%'}}>
                  <label>Link Type</label>
                  <select className="selects select" name="stream_type" onChange={this.handleChange} style={{width:'100%'}} required>
                    <option value="Web Link">Web Link</option>
                    <option value="Acestream">Acestream</option>
                </select>
                </div>
              </div>
              <br/>
              <button className="button button tag is-warning comp" style={{height: 'inherit',fontSize: '14px',margin:'auto 0 auto auto'}} type="submit">Submit</button>
    				</form>)
             : ''
         }
     </div>
   );
 }
}

const firebaseAppAuth = firebaseApp.auth();

const providers = {
 googleProvider: new firebase.auth.GoogleAuthProvider(),
};

export default withFirebaseAuth({
 providers,
 firebaseAppAuth,
})(Submit);
