import React, { Component } from 'react';
import { BrowserRouter as Router,Route,Link} from 'react-router-dom';

import HomeNBA from './components/nba/HomeNBA';
import GameNBA from './components/nba/Game';

import Contact from './components/Contact';
import Submit from './components/Submit';
import Dcma from './components/Dcma';
import Privacy from './components/Privacy';
import Rules from './components/Rules';
import 'bulma/css/bulma.css'
import './App.css';

class App extends Component {
	render() {
    return (
			<div id="app">
				<Router>
					<header>
						<div id="nav">
							<Link to="/">Home</Link> | <Link to="/submit">Submit</Link> | <Link to="/rules">Rules</Link>
						</div>
					</header>
					<main>
			      <div className="column is-mobile is-10-tablet is-offset-1-tablet is-8-desktop is-offset-2-desktop">
							<Route exact path={`/submit`} component={Submit} />
							<Route exact path={`/`} component={HomeNBA} />
							<Route exact path={`/rules`} component={Rules} />
							<Route exact path={`/contact`} component={Contact} />
							<Route exact path={`/dcma`} component={Dcma} />
							<Route exact path={`/privacy`} component={Privacy} />
							<Route exact path={`/game/:id/:title`} component={GameNBA} />
			      </div>
					</main>
					<footer>
						<div id="nav">
							<Link to="/">Home</Link> | <Link to="/privacy">Privacy</Link> | <Link to="/dcma">Dcma</Link> | <Link to="/contact">Contact</Link>
						</div>
					</footer>
				</Router>
			</div>
    );
	}
}

export default App;
