import React, { Component } from 'react';
import SkeletonHome from "../skeleton/SkeletonHome";
import LeagueItem from './LeagueItem';
import StreamService from "../../StreamService";

class HomeNBA extends Component {
	state = {
		leagues: [],
    interval: ""
	};
  async componentDidMount() {
		try {
      this.setState({leagues: await StreamService.getNBAFixture()});
      this.up();
    } catch (err) {
      this.state.error = err.message;
    }
	}
  componentWillUnmount() {
    clearInterval(this.state.interval);
  }
  async up() {
    this.state.interval = await setInterval(async () => {
      try {
        this.setState({leagues: await StreamService.getNBAFixture()});
      } catch (err) {
        this.state.error = err.message;
      }
    }, 15000);
  }
	render() {
    return (
      <div>
      {this.state.leagues.length > 0 ? this.state.leagues.map(
				(league) => (<LeagueItem key={league.id} league={league}/>)
			) : (<SkeletonHome/>)}
      </div>
    );
	}
}

export default HomeNBA;
