import React, { Component } from 'react';
import Score from './gameItems/Score';
import Team from './gameItems/Team';
const { DateTime } = require("luxon");

class GameItem extends Component {
  time() {
    return DateTime.fromISO(this.props.event.date).toLocaleString({
      hour: "2-digit",
      minute: "2-digit",
      hour12: false
    });
  }
  render(){
    return(
      <div className="box boxy">
        <div className="competition-cell-table">
          <Team team={this.props.event.competitors[0]}/>
          <Score event={this.props.event}/>
          <Team team={this.props.event.competitors[1]}/>
        </div>
        <span className="competition-cell-status">{this.props.event.status === 'pre' ? this.time() : this.props.event.summary}</span>
      </div>
    );
  }
}
export default GameItem;
