class StreamService {
    static getStreams(game) {
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url + 'event/' + game + '/streams');
                const data = res.data;
                const stream = [];
                data.forEach(doc => {
                    //if (doc.stream_type === "Web Link") {
                        stream.push({
                            user: doc.username,
                            stream_id: doc.stream_id,
                            link: doc.stream_link,
                            lang: doc.language,
                            mobile: doc.mobile_compatible,
                            quality: doc.misr,
                            ads: doc.ads_overlay,
                            channel: doc.channel_name,
                            adblock: doc.adblock === "OFF" ? "No" : "Yes",
                            nsfw: doc.nsfw,
                            type: doc.stream_type,
                            v: doc.verified === "Verified" ? 1 : 0
                        });
                    //}
                });
                resolve(
                    stream
                );
            } catch (err) {
                reject(err);
            }
        });
    }
    static getEvent(game) {
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url + 'event/' + game);
                const data = res.data;
                resolve(
                    data
                );
            } catch (err) {
                reject(err);
            }
        });
    }
    static getFixture() {
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url + 'fixture');
                const data = res.data;
                resolve(
                    data.sports[0].leagues
                );
            } catch (err) {
                reject(err);
            }
        });
    }
    static insertStream(game, stream) {
      return new Promise(async(resolve, reject) => {
          try {
              const data = await axios.post(url + 'nba/streams', {
                  game_id: game,
                  stream: stream
              });
              resolve(
                  data
              );
          } catch (err) {
              reject(err);
          }
      });

        /*return axios.post(url + 'nba/streams', {
            game_id: game,
            stream: stream
        });*/
    }

    static getNBAFixture() {
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url + 'nba/fixture');
                const data = res.data;
                resolve(
                    data.sports[0].leagues
                );
            } catch (err) {
                reject(err);
            }
        });
    }
    static getNBAEvent(game) {
        return new Promise(async(resolve, reject) => {
            try {
                const res = await axios.get(url + 'nba/event/' + game);
                const data = res.data;
                resolve(
                    data
                );
            } catch (err) {
                reject(err);
            }
        });
    }
}

export default StreamService;
