import React, { Component } from 'react';
import StreamService from "../../StreamService";
import SkeletonStreams from '../skeleton/SkeletonStreams';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-146349963-1');
class Streams extends Component {
  state = {
		streams: [],
    error:null
	};
  async componentDidMount() {
    try{
      this.setState({streams: await StreamService.getStreams(this.props.id)});
    }
    catch(err){
      this.setState({error: err});
    }
	}
  link(l) {
    ReactGA.ga('send', 'event', 'LinkOut', l);
    window.open(l, "_blank");
  }
  render(){
    return(
      <div>
      {this.state.streams.length > 0 && this.props.event.status.type.state !== 'post' ? (
        <div className="league h2h scrollable">
        <table className="tbd">
          <thead>
            <tr>
              <th></th>
              <th>User</th>
              <th>Channel</th>
              <th>Lan</th>
              <th>ads</th>
              <th>mobile</th>
              <th>quality</th>
              <th>Nsfw</th>
              <th>Adb</th>
            </tr>
          </thead>
          <tbody>
          {
            this.state.streams.sort((a, b) => {
              if (parseInt(a.ads) < parseInt(b.ads)) return -1;
              if (parseInt(a.ads) > parseInt(b.ads)) return 1;
              else return 0;
            }).sort((a, b) => {
              if (a.type === "Web Link" &&  b.type === "Acestream") return 1;
              if (b.type === "Web Link" &&  a.type === "Acestream") return -1;
              else return 0;
            }).sort((a, b) => {
              if (parseInt(a.v) < parseInt(b.v)) return 1;
              if (parseInt(a.v) > parseInt(b.v)) return -1;
              else return 0;
            }).map((item)=>(
              <tr key={item.stream_id} onClick={this.link.bind(this,item.link)}>
                <td className="verified">
                {item.v === 1 ?
                  (<img src="/v.png" width="15px" alt="verified"/>) : ''
                }
                </td>
                <td>
                  <span>{item.user}</span>
                </td>
                <td>
                  <span>{item.channel.slice(0,11).toLowerCase()}</span>
                </td>
                <td>
                  <span>{item.lang.toLowerCase()}</span>
                </td>
                <td>
                  <span>{item.ads}</span>
                </td>
                <td>
                  <span>{item.mobile}</span>
                </td>
                <td>
                  <span>
                    <b>{item.quality}</b>
                  </span>
                </td>
                <td>
                  <span>{item.nsfw}</span>
                </td>
                <td>
                  <span>{item.adblock}</span>
                </td>
              </tr>
            ))
          }
          </tbody>
        </table></div>) : this.state.error ? (<div className="league"><div className="notification has-text-danger" style={{backgroundColor: '#1c1f22'}}>No Streams Found For This Game</div></div>) : (<SkeletonStreams/>)}
      </div>
    );
  }
}
export default Streams;
