import React, { Component } from 'react';
import withFirebaseAuth from 'react-with-firebase-auth'
import * as firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import firebaseConfig from '../firebaseConfig';

let firebaseApp = '';
if (!firebase.apps.length)
  firebaseApp = firebase.initializeApp(firebaseConfig);
else
  firebaseApp = firebase.app();
const db = firebaseApp.firestore();

class Comment extends Component {
  constructor(props){
		super(props);
		this.state = {
			chats: [],
      message: ''
		}
	}

	handleChange = e => {
		this.setState({[e.target.name]: e.target.value});
	}

	handleSubmit = e => {
		e.preventDefault();
    db.collection('comments').add({
      msg: this.state.message,
			user: this.props.user.displayName,
			date: new Date().getTime(),
      game: this.props.id
    }).then(docRef => {
      docRef.update({id: docRef.id})
      this.setState({message:''});
      db.collection('comments').where('game', '==', this.props.id).get().then((querySnapshot) => {
        const data = querySnapshot.docs.map(doc => doc.data());
        this.setState({chats:data});
      })
    });
	}
	componentDidMount(){
    db.collection('comments').where('game', '==', this.props.id).get().then((querySnapshot) => {
      const data = querySnapshot.docs.map(doc => doc.data());
      this.setState({chats:data});
    })
	}
 render() {
   const {
     user,
     signOut,
     signInWithGoogle,
   } = this.props;

   return (
    <div className="league">
         {
           user
             ? <button className="button button tag is-warning comp" style={{height: 'inherit',fontSize: '14px'}} onClick={signOut}>Sign out</button>
             : <button className="button button tag is-warning comp" style={{height: 'inherit',fontSize: '14px'}} onClick={signInWithGoogle}>Sign in with Google to Comment</button>
         }
         {
           user
             ? <form className="send-chat" onSubmit={this.handleSubmit} style={{display:'flex',borderBottom: '2px solid #1c1f22'}}>
              <div className="control" style={{width:'87%'}}>
                <textarea className="input is-hovered" type="text" rows="3" name="message" id="message" value={this.state.message} onChange={this.handleChange}/>
              </div>
              <button className="button button tag is-warning comp" style={{height: 'inherit',fontSize: '14px',margin:'auto 0 auto auto'}} type="submit">comment</button>
    				</form>
             : ''
         }
         <div className="chatbox">
  					{this.state.chats.length === 0 ? (<div className="chat-box box"><p>There is no comments</p></div>) : this.state.chats.map(chat => {
  						return(
                <div className="chat-box box" key={chat.id}>
                  <div className="media-content">
                      <div className="content">
                        <p>
                          <strong className="has-text-success">{chat.user} : </strong>
                          <span>{chat.msg}</span>
                          <small style={{float: 'right'}}>{new Date(chat.date).toLocaleString({ hour: '2-digit', minute: '2-digit', hour12: false })}</small>
                        </p>
                      </div>

                    </div>
                </div>
  						);
  					})}
			   </div>
     </div>
   );
 }
}

const firebaseAppAuth = firebaseApp.auth();

const providers = {
 googleProvider: new firebase.auth.GoogleAuthProvider(),
};

export default withFirebaseAuth({
 providers,
 firebaseAppAuth,
})(Comment);
