import React, { Component } from 'react';

class Score extends Component {
  render(){
    return(
      <span className="competition-cell-table-cell competition-cell-score">
        {this.props.event.status === 'post' ? (<span>{this.props.event.competitors[0].score} - {this.props.event.competitors[1].score}</span>) : this.props.event.status === 'in' ? (<span className="tag is-success live">Live</span>):(<span className="tagy">vs</span>)}
      </span>
    );
  }
}
export default Score;
