import React, { Component } from 'react';

class Stats extends Component {
  render(){
    return(
      <div className="league h2h scrollable">
        <span style={{float:'left'}}>
          <span className="tag is-warning comp">{this.props.boxscore.teams[1].team.shortDisplayName}</span>
        </span>
        <span className="tag comp">Stats</span>
        <span style={{float:'right'}}>
          <span className="tag is-warning comp">{this.props.boxscore.teams[0].team.shortDisplayName}</span>
        </span>
        <div className="stats">
          <table>
            <tbody>
            {this.props.boxscore.teams[1].statistics.map((stath,i)=>(
              <tr key={stath.label+i}>
                <td data-home-away="home">{stath.displayValue}</td>
                <td>{stath.label}</td>
                <td data-home-away="away">{this.props.boxscore.teams[0].statistics[i].displayValue}</td>
              </tr>
            ))}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}
export default Stats;
